import React,{useState, useEffect} from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { Link as RouterLink } from 'react-router-dom';
import { motion } from "framer-motion";
import axios,{instance2} from '../../axios';
import { css } from "styled-components/macro"; //eslint-disable-line
import { SectionHeading } from "components/misc/Headings.js";
import { Container, ContentWithPaddingXl } from "components/misc/Layouts.js";
import { fToNow } from '../../utils/formatTime';
const Row = tw.div`flex flex-col lg:flex-row -mb-10`;
const Heading = tw(SectionHeading)`text-left lg:text-4xl xl:text-5xl`;

const PopularPostsContainer = tw.div`lg:w-2/3`;
const PostsContainer = tw.div`mt-12 flex flex-col sm:flex-row sm:justify-between lg:justify-start`;
const Post = tw(motion.a)`block sm:max-w-sm cursor-pointer mb-16 last:mb-0 sm:mb-0 sm:odd:mr-8 lg:mr-8 xl:mr-16`;
const Image = styled(motion.div)(props => [
  `background-image: url("${props.imageSrc}");`,
  tw`h-64 bg-cover bg-center rounded`
]);
const Title = tw.h5`mt-6 text-xl font-bold transition duration-300 group-hover:text-primary-500`;
const Description = tw.p`mt-2 font-medium text-secondary-100 leading-loose text-sm`;
const AuthorInfo = tw.div`mt-6 flex items-center`;
const AuthorImage = tw.img`w-12 h-12 rounded-full`;
const AuthorNameAndProfession = tw.div`ml-4`;
const AuthorName = tw.h6`font-semibold text-lg`;
const AuthorProfile = tw.p`text-secondary-100 text-sm`;

const RecentPostsContainer = styled.div`
  ${tw`mt-24 lg:mt-0 lg:w-1/3`}
  ${PostsContainer} {
    ${tw`flex flex-wrap lg:flex-col`}
  }
  ${Post} {
    ${tw`flex justify-between mb-10 max-w-none w-full sm:w-1/2 lg:w-auto sm:odd:pr-12 lg:odd:pr-0 mr-0`}
  }
  ${Title} {
    ${tw`text-base xl:text-lg mt-0 mr-4 lg:max-w-xs`}
  }
  ${AuthorName} {
    ${tw`mt-3 text-sm text-secondary-100 font-normal leading-none`}
  }
  ${Image} {
    ${tw`h-20 w-20 flex-shrink-0`}
  }
`;
const PostTextContainer = tw.div``

export default () => {
  // This setting is for animating the post background image on hover
  const postBackgroundSizeAnimation = {
    rest: {
      backgroundSize: "100%"
    },
    hover: {
      backgroundSize: "110%"
    }
  };
  const [result1, setResult1] = useState([])
  const [result2, setResult2] = useState([])

  
  useEffect(()=>{
    console.log("effect1")
    getBlogs1();
    getBlogs2();
    
  },[])

  async function getBlogs1() {
    await axios.get(`/pblog/list/1/2`)
      .then(response => {
        setResult1(response.data);
        // console.log(response.data.blogs[0])
      })
      .catch(error => "error");
    // console.log(result)
    return result1
  }
  async function getBlogs2() {
    await axios.get(`/pblog/list/3/4`)
      .then(response => {
        setResult2(response.data);
        // console.log(response.data.blogs[0])
      })
      .catch(error => "error");
    // console.log(result)
    return result2
  }
  

  return (
    <Container>
      <ContentWithPaddingXl>
        <Row>
          <PopularPostsContainer>
            <Heading>Popular Posts</Heading>
            <PostsContainer>
              {result1.map((post) => (
                <Post key={post.id}  className="group" initial="rest" whileHover="hover" animate="rest">
                  <RouterLink to={`/${post.category_name}/${post.url}`}>
                  <Image
                    transition={{ duration: 0.3 }}
                    variants={postBackgroundSizeAnimation}
                    imageSrc={`${instance2.defaults.baseURL}storage/blog/${post.image1}`}
                  />
                  <Title>{post.title}</Title>
                  {/* <Description>{post.short_description}</Description> */}
                  <AuthorInfo>
                    {/* <AuthorImage src={post.authorImageSrc} /> */}
                    <AuthorNameAndProfession>
                      <AuthorName>{post.category_name}</AuthorName>
                      <AuthorProfile>{fToNow(post.created_at)}</AuthorProfile>
                    </AuthorNameAndProfession>
                  </AuthorInfo>
                  </RouterLink>
                </Post>
              ))}
            </PostsContainer>
          </PopularPostsContainer>
          <RecentPostsContainer>
            <Heading>Recent Posts</Heading>
            <PostsContainer>
              {result2.map((post) => (
              <Post key={post.id}  className="group">
              
                <PostTextContainer>
                <RouterLink to={`/${post.category_name}/${post.url}`}>
                  <Title>{post.title}</Title>
                  </RouterLink>
                  <div style={{display:'flex'}}>
                    <AuthorName style={{fontWeight:"bold"}}>{post.category_name }</AuthorName> 
                    <AuthorProfile style={{marginTop:"9px", marginLeft:"20px"}}>{fToNow(post.created_at)}</AuthorProfile>
                  </div>
                </PostTextContainer>
                <Image imageSrc={`${instance2.defaults.baseURL}storage/blog/${post.image1}`} />
                
              </Post>
              ))}
            </PostsContainer>
          </RecentPostsContainer>
        </Row>
      </ContentWithPaddingXl>
    </Container>
  );
};
