import React from "react";
import axios, { instance2 } from '../../axios';
import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom'
import { Helmet } from "react-helmet";
import { fToNow, fDate, fDateTime, fDateTimeSuffix } from '../../utils/formatTime';
import styled from "styled-components";
import loader from "../../images/loader.gif";
import tw from "twin.macro";
import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import Hero from "components/hero/BackgroundAsImageWithCenteredContent.js";
import { SectionHeading, Subheading as SubheadingBase } from "components/misc/Headings.js";
import { Container, ContentWithPaddingXl } from "components/misc/Layouts.js";
import { SectionDescription } from "components/misc/Typography.js";
import Features from "components/features/ThreeColSimple.js";
import MainFeature from "components/features/TwoColSingleFeatureWithStats.js";
import SliderCard from "components/cards/ThreeColSlider.js";
import TrendingCard from "components/cards/TwoTrendingPreviewCardsWithImage.js";
import Blog from "components/blogs/ThreeColSimpleWithImage.js";
import Blog1 from "components/blogs/GridWithFeaturedPost.js";
import Testimonial from "components/testimonials/TwoColumnWithImageAndProfilePictureReview.js";
import FAQ from "components/faqs/SimpleWithSideImage.js";
import SubscribeNewsLetterForm from "components/forms/SimpleSubscribeNewsletter.js";
import Footer from "components/footers/FiveColumnWithInputForm.js";



const Heading = tw(SectionHeading)``;
const Subheading = tw(SubheadingBase)`text-center mb-3`;
const Description = tw(SectionDescription)`text-center mx-auto`;
const ThreeColumnContainer = styled.div`
  ${tw`mt-10 flex flex-col items-center lg:items-stretch lg:flex-row flex-wrap lg:justify-center max-w-screen-lg mx-auto`}
`;
const Column = styled.div`
  ${tw`lg:w-1/3 max-w-xs`}
`;

const Card = styled.a`
  ${tw`flex flex-col items-center text-center h-full mx-4 px-4 py-8 rounded transition-transform duration-300 hover:cursor-pointer transform hover:scale-105 `}
  .imageContainer {
    ${tw`text-center rounded-full p-4 bg-gray-100`}
    img {
      ${tw`w-8 h-8`}
    }
  }

  .title {
    ${tw`mt-4 font-bold text-xl leading-none`}
  }

  .description {
    ${tw`mt-4 text-sm font-medium text-secondary-300`}
  }

  .link {
    ${tw`mt-auto inline-flex items-center pt-5 text-sm font-bold text-primary-300 leading-none hocus:text-primary-900 transition duration-300`}
    .icon {
      ${tw`ml-2 w-4`}
    }
  }
`;

export default () => {

  const [modalIsOpen, setModalIsOpen] = useState(false);

  const toggleModal = () => setModalIsOpen(!modalIsOpen);

  const [result, setResult] = useState([])
  const [blogDetails, setBlogDetails] = useState([])
  
  let params = useParams();
  let blogUrl = params.url;
  // alert(blogUrl)
  useEffect(() => {
    //console.log("effect1")
    window.scrollTo(0, 0)
    getBlogs();

  }, [])

  useEffect(() => {
    //console.log("effect2")
    getBlogs();
    window.scrollTo(0, 0)
  }, [blogUrl])

  async function getBlogs() {

    await axios.get(`pblog/single/${blogUrl}`)
      .then(response => {
        setResult(response.data.blog);
        setBlogDetails(response.data.blogDetails)
        
        // console.log(response.data)
        if(response.data.msg == "Not Found"){
          window.location = "/404"
        }
      })
      .catch(error => "error");
    // console.log(result)
    return result
  }

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{result.title}</title>
        <meta name="description" content={result.short_description}/>
        <meta name="keywords" content={result.keywords}/>
        <link rel="canonical" href="" />
      </Helmet>
      <AnimationRevealPage>
        {
          result.title ?
            <Hero data={result} />
            :
            <img
              src={loader}
              alt="Hero"
              style={{ borderRadius: "40px", marginLeft:"20%" }}
            />
        }
        <Container >
          <ContentWithPaddingXl style={{ paddingTop: "10px" }}>
            <Description style={{ maxWidth: "60rem", color: "#333" }}>


              <p dangerouslySetInnerHTML={{ __html: result.short_description }}></p> <br />
              <p dangerouslySetInnerHTML={{ __html: result.long_description }}></p>

            </Description>
            {
              blogDetails?.map((detail) => (
                <>

                  <Description style={{ maxWidth: "60rem", color: "#333" }}>
                    <img
                      src={`${instance2.defaults.baseURL}storage/blog/${detail.image}`}
                      alt=""
                      style={{ borderRadius: "20px" }}
                    />
                    <br />
                    <p dangerouslySetInnerHTML={{ __html: detail.description }}></p>

                  </Description>
                </>

              ))
            }

          </ContentWithPaddingXl>
        </Container>
        <Blog catId={result.category_id} />
        <Footer />
      </AnimationRevealPage>
    </>
  )

}
