import React, { useState, useEffect } from "react";
import { useParams } from 'react-router-dom'
import axios, { instance2 } from '../axios';
import { Helmet } from "react-helmet";
import { Link as RouterLink } from 'react-router-dom';
import AnimationRevealPage from "helpers/AnimationRevealPage.js";
import { Container, ContentWithPaddingXl } from "components/misc/Layouts";
import tw from "twin.macro";
import styled from "styled-components";
import { css } from "styled-components/macro";
import Header from "components/headers/light.js";
import Footer from "components/footers/FiveColumnWithInputForm.js";
import { SectionHeading } from "components/misc/Headings";
import { PrimaryButton } from "components/misc/Buttons";
import { fToNow, fDate } from '../utils/formatTime';
const HeadingRow = tw.div`flex`;
const Heading = tw(SectionHeading)`text-gray-900`;
const Posts = tw.div`mt-6 sm:-mr-8 flex flex-wrap`;
const PostContainer = styled.div`
  ${tw`mt-10 w-full sm:w-1/2 lg:w-1/3 sm:pr-8`}
  ${props =>
    props.featured &&
    css`
      ${tw`w-full!`}
      ${Post} {
        ${tw`sm:flex-row! h-full sm:pr-4`}
      }
      ${Image} {
        ${tw`sm:h-96 sm:min-h-full sm:w-1/2 lg:w-2/3 sm:rounded-t-none sm:rounded-l-lg`}
      }
      ${Info} {
        ${tw`sm:-mr-4 sm:pl-8 sm:flex-1 sm:rounded-none sm:rounded-r-lg sm:border-t-2 sm:border-l-0`}
      }
      ${Description} {
        ${tw`text-sm mt-3 leading-loose text-gray-600 font-medium`}
      }
    `}
`;
const Post = tw.div`cursor-pointer flex flex-col bg-gray-100 rounded-lg`;
const Image = styled.div`
  ${props => css`background-image: url("${props.imageSrc}");`}
  ${tw`h-64 w-full bg-cover bg-center rounded-t-lg`}
`;
const Info = tw.div`p-8 border-2 border-t-0 rounded-lg rounded-t-none`;
const Category = tw.div`uppercase text-primary-500 text-xs font-bold tracking-widest leading-loose after:content after:block after:border-b-2 after:border-primary-500 after:w-8`;
const CreationDate = tw.div`mt-4 uppercase text-gray-600 italic font-semibold text-xs`;
const Title = tw.div`mt-1 font-black text-base text-gray-900 group-hover:text-primary-500 transition duration-300`;
const Description = tw.div``;

const ButtonContainer = tw.div`flex justify-center`;
const LoadMoreButton = tw(PrimaryButton)`mt-16 mx-auto`;

export default ({
  posts = []
}) => {
  const [visible, setVisible] = useState(7);
  const onLoadMoreClick = () => {
    setVisible(v => v + 6);
  };
  let params = useParams();
  let catUrl = params.url;

  const [result, setResult] = useState([])
  const [catname, setCatName] = useState([])

  // alert(blogUrl)
  useEffect(() => {
    console.log("effect1")
    getBlogs();

  }, [])
  useEffect(() => {
    console.log("effect1")
    getBlogs();
    window.scrollTo(0, 0)
  }, [catUrl])

  async function getBlogs() {

    await axios.get(`pblog/category/${catUrl}`)
      .then(response => {
        setResult(response.data);
        setCatName(response.data[0].category_name)
        console.log(response.data[0])
      })
      .catch(error => "error");
    // console.log(result[0])
    return result
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{catname}</title>
        <link rel="canonical" href="" />
      </Helmet>
      <AnimationRevealPage>
        <Header />
        <Container>
          <ContentWithPaddingXl>
            {/* <HeadingRow>
            <Heading>{catname}</Heading>
          </HeadingRow> */}
            <Heading>{catname}</Heading>

            <Posts>
              {
                result.map((post, index) => (
                  <PostContainer key={index} featured={index == 0 ? true : false}>
                    <Post className="group">

                      <Image imageSrc={`${instance2.defaults.baseURL}storage/blog/${post.image1}`} />

                      <Info>
                        <RouterLink to={`/${post.category_name}/${post.url}`}>
                          <Category>{post.category_name}</Category>
                          <CreationDate>
                            {
                              post.date ?
                                fDate(post.date)
                                : null
                            }
                          </CreationDate>
                          <Title>{post.title}</Title>
                          <Description>{index == 0 ? <p dangerouslySetInnerHTML={{ __html: post.short_description }}></p> : false}</Description>
                        </RouterLink>
                      </Info>

                    </Post>
                  </PostContainer>
                ))}
            </Posts>
            {visible < posts.length && (
              <ButtonContainer>
                <LoadMoreButton onClick={onLoadMoreClick}>Load More</LoadMoreButton>
              </ButtonContainer>
            )}
          </ContentWithPaddingXl>
        </Container>
        <Footer />
      </AnimationRevealPage>
    </>
  );
};


